import "./style.scss";
import PropTypes from "prop-types";

const { forwardRef } = require("react");

const propTypes = {
    id: PropTypes.string,
    className: PropTypes.string,
    ref: PropTypes.oneOfType([PropTypes.func, PropTypes.shape({ current: PropTypes.any })]),

    value: PropTypes.string,
    required: PropTypes.bool,

    onChange: PropTypes.func,
    onFocus: PropTypes.func,
    onBlur: PropTypes.func,
    onKeyUp: PropTypes.func,

    style: PropTypes.object,
    placeholder: PropTypes.string,
};

const TextareaX = forwardRef((props, ref) => {
    const { value, onChange, placeholder = "Start typing ...", ...rest } = props;
    const style = props.style;

    return (
        <div className="TextareaX">
            <span id="Mirror" {...{ style }}>{`${value}\n`}</span>

            <div className="Input">
                <textarea {...rest} {...{ value, onChange, placeholder, style }} ref={ref} />
            </div>
        </div>
    );
});

TextareaX.propTypes = propTypes;

export default TextareaX;
